.message {
  padding: var(--content-spacing);
  padding-top: 15px;
  padding-bottom: 15px;
  white-space: pre-wrap;
  line-height: 1.3;
  position: relative;
}

.message.reminder-returned {
    padding-left: 40px;
    padding-top: 45px;
    padding-bottom: 35px;
}

.message .border {
    position: absolute;
    top: 0;
    left: 0;
    width: 7.5px;
    height: 100%;
    background: transparent;
}

.message.group .border {
    left: 10px;
    background: #ddd;
}

.message.selected .border {
    background: var(--snowpurple);
}

.message.group {
    padding-top: 10px;
    padding-bottom: 10px;
}

.message .reminder-header {
    position: absolute;
    left: 20px;
    top: 15px;
    color: var(--slate);
    font-weight: 500;
    padding-left: 15px;
}

.message .reminder-header label {
    color: var(--gray);
    margin-left: 20px;
}

.message .reminder-header::before {
    content: '';
    width: 10px;
    height: 10px;
    position: absolute;
    background: var(--pink);
    border-radius: 100%;
    top: 5px;
    left: 0px;
}

.message .quote-border {
    position: absolute;
    top: 45px;
    left: 20px;
    background: var(--lightgray);
    width: 7.5px;
    height: calc(100% - 85px);
}

.message.selected {
  background: var(--snowbg);
}

.message.selected.group .border {
    background: var(--snowpurple);
}

.message.selected.editing .border {
    background: transparent;
}

.message header {
  color: var(--gray);
  font-weight: 500;
  margin-bottom: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.message header .username {
  color: var(--slate);
  padding-right: 15px;
  cursor: pointer;
}

.message .reminder {
  color: var(--pink);
  padding-top: 5px;
  font-weight: 500;
}

.message header .quote {
  color: var(--gray);
  padding-left: 15px;
}

.message .body {
  padding: 2.5px 0;
  cursor: default;
}

.message .edited {
  color: var(--smoke);
  font-weight: 400;
  margin-left: 5px;
}

.message .body .inline-editor {
  border: 1px solid var(--snowpurple);
  border-left: 7px solid var(--snowpurple);
  border-radius: 2px;
  padding: 5px;
  background: #fff;
}


.message.placeholder .body {
    margin: 20px 0 0 0;
}

.message .link {
    color: var(--blue);
    font-weight: 500;
    margin-top: 10px;
    cursor: pointer;
}

.message.reminder-returned .link {
    position: absolute;
    bottom: 10px;
    left: 20px;
}

.message.group.selected {
    border-color: var(--snowbg);
}

.quote-block {
    margin: 5px 0;
    padding: 0 0 5px 30px;
    color: var(--gray);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
