.commandbar {
  display: grid;
  grid-row-gap: 20px;
  background: var(--smoke);
  border-radius: 2px;
  min-width: 450px;
}

.commandbar .heading {
  font-weight: 500;
  color: var(--gray);
  padding: 15px 50px;
  position: relative;
}

.commandbar .heading label {
  position: absolute;
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 100%;
  background: #fff;
  margin-left: -25px;
  margin-top: -1px;
}

.commandbar .input input {
  border: 0;
  background: transparent;
  display: block;
  border-left: 7.5px solid var(--snowpurple);
  font-size: 18px;
  padding: 10px 20px;
  color: var(--gray);
  caret-color: #fff;
}

.commandbar .commands {
  font-size: 14px;
  color: var(--gray);
  font-weight: 500;
  padding: 10px 0;
}

.commandbar .commands .row {
  padding: 15px 25px;
}

.commandbar .commands .row.selected {
  background: rgba(0, 0, 0, 0.075);
}
