.textfield {
  display: grid;
}

.textfield::after {
  content: attr(data-replicated-value) ' ';
  white-space: pre-wrap;
  visibility: hidden;
}

.textfield > textarea {
  resize: none;
  overflow: hidden;
}

.textfield > textarea,
.textfield::after {
  font: inherit;
  grid-area: 1 / 1 / 2 / 2;
}

.textfield textarea {
  display: block;
  width: 100%;
  color: var(--black);
  border: 0;
  font: 500 14px -apple-system, BlinkMacSystemFont, sans-serif;
  outline: none;
}
