:root {
  --black: rgba(0, 0, 0, 0.8);
  --smoke: #c3cfd9;
  --slate: rgb(41, 56, 69);
  --gray: #788896;
  --lightgray: #DFE6ED;
  --dark-slate: rgb(21, 36, 49);
  --snowbg: #f3f6fa;
  --snowpurple: #afb1dc;
  --pink: #bd33d1;
  --blue: #2c87d9;

  --nav-spacing: 7.5px 20px;
  --content-spacing: 7.5px 30px;
}

* {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
  outline: none;
}

html,
body,
.app {
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 14px;
  font-smoothing: antialiased !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  color: rgba(0, 0, 0, 0.8);
  overflow: hidden;
}

.app {
  -webkit-app-region: drag;
  background: #fff;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  display: grid;
  grid-template-columns: 200px auto;
  height: 100%;
}

.container.sidebar-on {
  grid-template-columns: 200px auto 200px;
}

.navigation {
  border-right: 1px solid var(--smoke);
}

.logo {
  font-size: 16px;
  letter-spacing: 0.03rem;
  font-weight: 500;
  color: var(--black);
  padding: var(--nav-spacing);
  margin: 30px 0;
}

.separator {
  border-bottom: 1px solid var(--smoke);
  margin: 20px;
}

.folders {
  margin-top: 20px;
}

.folder {
  font-size: 12.5px;
  padding: 7.5px 0;
  font-weight: 500;
  color: var(--slate);
  cursor: pointer;
  padding: var(--nav-spacing);
  border-left: 4px solid transparent;
  position: relative;
}

.folder.inactive {
  color: var(--gray);
}

.folder.selected,
.folder:hover {
  color: var(--dark-slate);
  /*background: var(--snowbg);*/
  /*border-color: var(--snowpurple);*/
}

.dms h3 {
  color: var(--smoke);
  font-size: 12.5px;
  font-weight: 500;
  padding: var(--nav-spacing);
}

.unread-icon {
  width: 8px;
  height: 8px;
  background: var(--snowpurple);
  position: absolute;
  border-radius: 100%;
  left: 5px;
  top: calc(50% - 4px);
}

.com {
    display: grid;
    grid-template-columns: auto 310px;
}

.com .channel {
  border-right: 1px solid var(--smoke);
  display: flex;
  flex-flow: column;
}

.com .channel-sidebar {
  border-right: 1px solid var(--smoke);
} 

.channel-com-switcher {
  height: 75px;
  margin: 30px 0 0px 0;
  padding: var(--content-spacing);
}

.channel-com-switcher div {
  display: inline;
  font-size: 16px;
  font-weight: 500;
  margin-right: 25px;
  cursor: pointer;
  color: var(--smoke);
}

.channel-com-switcher div:last-child {
  margin-right: 0;
}

.channel-com-switcher .channel-title,
.com-dm-title {
  color: var(--black);
  text-transform: capitalize;
}

.channel-com-switcher div.on {
  color: var(--gray);
}

.channel-com-switcher div:hover {
  color: var(--black);
}

.channel-com-switcher div label {
  position: absolute;
  font-size: 11px;
  margin-left: 5px;
}

.com-dm-title {
  margin: 30px 0;
  padding: var(--content-spacing);
  font-size: 16px;
  font-weight: 500;
}

.chat {
  width: 100%;
  height: calc(100vh - 105px);
  outline: none;
  display: grid;
  grid-template-rows: 1fr auto;
}

.messages {
  display: block;
  overflow: scroll;
  display: flex;
  flex-direction: column-reverse;
}

.compose {
  display: block;
  position: relative;
}

.compose .textfield > textarea,
.compose.textfield::after {
  padding: 7.5px 30px 7.5px 23px;
}

.compose .draft-reminder {
  padding: var(--content-spacing);
  padding-top: 0px;
  padding-bottom: 5px;
  color: var(--gray);
  font-size: 12px;
  font-weight: 500;
}

.compose .textfield {
  padding-bottom: 5px;
  padding-top: 5px;
}

.compose .textfield textarea {
  display: block;
  width: 100%;
  color: black;
  border: 0;
  font: 500 14px -apple-system, BlinkMacSystemFont, sans-serif;
  outline: none;
}

.compose-inner {
  border: 1px solid var(--smoke);
  border-radius: 2px;
  margin-bottom: 5px;
}

.compose .textfield {
  background: #fff;
  border-left: 7px solid #fff;
}

.compose.writing .textfield {
  border-left: 7px solid var(--snowpurple);
}

.threads .row {
  padding: var(--content-spacing);
  padding-top: 10px;
  padding-bottom: 10px;
  display: grid;
  grid-template-columns: 125px auto 40px;
  grid-column-gap: 10px;
  border-left: 2px solid transparent;
  cursor: pointer;
}

.threads .row:first-child,
.threads .row:hover {
  color: var(--dark-slate);
  background: var(--snowbg);
  border-color: var(--snowpurple);
}

.threads .row .author {
  color: var(--black);
  font-weight: 500;
}

.threads .row .subject {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.threads .row .subject label {
  color: var(--gray);
  padding-left: 15px;
}

.threads .row .date {
  text-align: right;
  color: var(--slate);
}

.sidebar {
}

.notification {
  position: absolute;
  bottom: 50px;
  right: 50px;
  width: 270px;
  display: grid;
  grid-template-columns: auto 50px;
  font-weight: 500;
  border: 1px solid var(--smoke);
  background: #fff;
  border-left: 5px solid var(--blue);
  padding: 10px 10px;
  font-size: 13px;
}

.notification .undo {
  color: var(--blue);
  text-transform: uppercase;
  text-align: center;
}

.notification .message {
  color: var(--gray);
  padding-left: 5px;
}

.thread {
    margin: 35px 0 0px 0;
    display: grid;
    grid-template-rows: 50px 1fr;
    height: calc(100vh - 35px);
}

.thread > header {
    font-size: 16px;
    font-weight: 500;
    color: var(--black);
    margin-bottom: 30px;
    padding: 0 20px;
}

.thread > header > span {
    color: var(--smoke);
    margin-left: 10px;
    font-size: 14px;
}

.thread .message {
    padding: 15px 20px 15px 17px;
}



.thread .compose {

}

.thread .content {
    overflow: scroll;
    height: 100%;
}
